@import url('https://fonts.googleapis.com/css2?family=Dongle:wght@300;400&display=swap');

$color-1: #546fe4;
$color-2: #212040;
$color-3: rgb(100,100,100);
$white: rgb(238, 238, 238);
$black: #060d2c;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background: $color-2;
    color: $white;
    font-family: 'Dongle', sans-serif;
}
li {
    list-style: none;
}
#results{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}