
 header { 
    .nav-container {
        position: absolute;
        top: 16px;
        left: 16px;
        font-size: 1.3rem;
    
         ul {
                display: flex;
                // width: 180px;
                justify-content: space-between;

                a{
                    text-decoration: none;
                    border-radius: 10px;
                    color: $white;
                    padding: 4px 8px;
                    margin-right: 5px;
                    width: 102px;

                    &:visited {
                        text-decoration: none;
                        color: $white;
                    }
                    &:hover{
                        background:$black;
                    }
                    &.nav-active { // Noter que le & est utilisé pour référencer l'élément a actuel
                    background: $color-1;
                    }
                }
                
        
            }
        }

}

// Exemple de media query pour les écrans de moins de 768px de largeur
@media screen and (max-width: 768px) {
    header .nav-container {
        width: 100%;
        text-align: center; 
        left: 0;
        
        ul {
            padding: 3px;
            width: 100%;
            position: fixed;
            justify-content: center;
            z-index: 100;
            background:#403f4c60;
            border: 2px solid #fff;
        
            a{
                padding: 0 10px;
            }
                
      }
    }
  }