.movie-container {
    flex: 1 0 25%;
    height: 620px;
    min-width: 260px;
    max-width: 280px;
    background: #060d2c;
    border-radius: 35px;
    margin: 5px;
    padding: 26px 15px 30px;
    overflow: hidden;
    position: relative;

    img{
        width: 80%;
        height: 254px;
        object-fit: cover;
        object-position: center;
        margin: 0 auto;
        display: block;
        border-radius: 50px;
        box-shadow: 0 8px 1px rgba(255,255,255,0.1), 0 16px 1px rgba(255,255,255,0.05);
    }
    h2{
        color:red;
        margin: 24px 0 0;
        letter-spacing: 1px;
        line-height: 20px;
        
    }
    #date{
        color: $color-3;
        letter-spacing: 1px;
        font-size: 1.2rem;
        font-weight: 300;
    }
    .note-container{
        display: flex;
        align-items: center;
        gap: 5px;
        margin-left: 2px;

        #note{
            letter-spacing: 1px;
            font-size: 1.2rem;
            display: block;
            // margin-top: -4px;
        }
        h5{
            font-size: 0.7rem; 
        }
    }
    ul{
        margin-left: -4px;
        span {
            display: inline-block; // Assure que chaque span prend l'espace dont il a besoin
            margin: 0 4px 4px 0;
            background:#212040;
            border-radius: 18px;
            padding: 0px 7px;   
            font-size: 1.1rem;
    }
    
        // &:first-of-type{
        //     margin-left: -40px;
        //     color: red;
        // }
    }
    #overview{
        margin-top: 5px;
        color: $color-3;
        font-size: 1.3rem;
        line-height: 20px;
        height: 160px; // c'est ce paramètre qui fait passer la fin du text derriere le bouton coup de coeur.
        overflow-y: scroll; //permet le scroll pour voir la fin du texte.
    }
    #cdc{
        position: absolute;
        left: 50%;
        bottom: 16px;
        transform: translateX(-50%);
        background: #546fe4;
        padding: 14px 0;
        width: 150px;
        text-align: center;
        border-radius: 15px;
        cursor: pointer;
        color: $white;
        border: none;

        
        &:hover {
            background: #212040;
        }

        &:focus {
            box-shadow: 0 0 0 1px rgb(58, 57, 110);
            animation: anim-shadow .3s forwards;
        }
        @keyframes anim-shadow {
            100% {
                box-shadow: 0 0 50px 20px rgb(112, 111, 211,0);
            }
            
        }
    }

}

