header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.search-container{
    display: grid;
    justify-content: center;
    align-items: center;
    #top,#flop{
        width: 300px;
    }

    input[type="text"] { // Ne pas mettre d'espace entre input et [type="text"], sinon ça ne prend pas
        border-radius: 10px 10px 0 0;
        border: none;
        width: 220px;
        font-size: 1.1rem;
        margin: 0 auto;
        outline: none; // Désactive la bordure bleu par défaut du navigateur quand je focus dans l'input
        text-align: center;
        padding: 1px 10px;
    }
}
.sort-container{
    
    #top{
        border-radius: 0 0 0 10px;

        span {
            display: inline-block;
            transform: rotate(-90deg);
        }
    }
    #flop{
        border-radius: 0 0 10px 0;

        span{
            display: inline-block;
            transform: rotate(90deg);
        }
    }
    #top,#flop{
        background: $color-1;
        width: 110px;
        cursor: pointer;
        color: $white;
        outline: none;
        border: none;
        font-size: 1.1rem;

        &:hover{
         background: $black;
        }
    }
}



